<template>
	<div>
		<div class="header">
			<div class="icon-left" @click="$router.goBack()"><img src="../../../static/img/icon/release_icon/return@2x.png"/></div>
			<div class="title">创建植物</div>
			<div class="success" @click="submit">完成</div>
		</div>
		<div class="header-null"></div>
		<div class="createBotany-content">
			<div class="createBotany-updata">
				<div class="createBotany-location">
					<img src="../../../static/img/icon/find_icon/location.png"/>
					<div>{{weather.city}}</div>
					<div>{{weather.temperature}}℃</div>
				</div>
				<div class="createBotany-updata-img">
					<div class="imgs" v-for="(item,index) in imgs">
						<img :src="item"/>
						<img src="../../../static/img/icon/denglu_gengxin_icon/guanbi@3x.png" @click="deleted(index)"/>
					</div>
					<div class="add-img">
						<img src="../../../static/img/icon/find_icon/img.png"/>
						<input type="file" @change="fileImg($event)"/>
					</div>
				</div>
			</div>
		</div>
		<div class="createBotany-address">
			<img src="../../../static/img/icon/find_icon/rili.png"/>
			<div @click="date">默认日期&nbsp;&nbsp;&nbsp;{{dates}}</div>
		</div>
		<div class="createBotany-bottom">
			<div class="createBotany-name">
				<div class="createBotany-name-title">
					植物名字（{{botanyName.length}}/32）
				</div>
				<textarea maxlength="32" placeholder="这是什么植物呢？" v-model="botanyName">{{botanyName}}</textarea>
			</div>
			<div class="createBotany-dynamic">
				<div class="createBotany-dynamic-title">
					Ta的动态（{{botanyDynamic.length}}/280）
				</div>
				<textarea maxlength="280" placeholder="写下今天Ta的变化" v-model="botanyDynamic">{{botanyDynamic}}</textarea>
			</div>
		</div>
	</div>
</template>

<script>
	import {getData} from '../../service/getData'
	export default {
		data() {
			return {
				botanyName: '',
				botanyDynamic: '',
				dates: '',
				imgs: [],
				weather: {}
			}
		},
		methods: {
			date() {
				var dtpicker = new mui.DtPicker({
				    type: "date",//设置日历初始视图模式 
				    beginDate: new Date(),//设置开始日期 
				    endDate: new Date(2019, 11, 31),//设置结束日期 
				    labels: ['Year', 'Mon', 'Day'],//设置默认标签区域提示语  
				}) 
				var _this = this
				dtpicker.show(function(e) {
				    _this.dates = e.value;
				}) 
			},
			fileImg(ev) {
				var _this = this
				var reader = new FileReader();
				reader.readAsDataURL(ev.target.files[0]);
				reader.onload=function(e) {
			        _this.imgs.push(e.target.result);
			    };
			},
			deleted(index) {
				this.imgs.splice(index, 1)
			},
			submit() {
				getData('/plantDiary/appAddPlantDiary', {
					token: sessionStorage.token,
					name: this.botanyName,
					address: this.weather.city + ',' + this.weather.temperature,
					img: this.imgs[0],
					plantTime: this.dates,
					desc: this.botanyDynamic
				}).then(d => {
					
				})
			}
		},
		mounted() {
			//获取今天的日期
			var date = new Date()
			var year = date.getFullYear()
			var month = date.getMonth()+1
			if(month < 10) {
				month = '0'+ month
			}
			var day = date.getDate()
			this.dates = year+'-'+month+'-'+day
			
			//高德地图获取城市和天气
			var map = new AMap.Map('container', {
			  resizeEnable: true
			})
			
			var _this = this
			
			map.plugin('AMap.Geolocation', function() {
			  var geolocation = new AMap.Geolocation({
			    // 是否使用高精度定位，默认：true
			    enableHighAccuracy: true,
			    // 设置定位超时时间，默认：无穷大
			    timeout: 10000,
			    // 定位按钮的停靠位置的偏移量，默认：Pixel(10, 20)
			    buttonOffset: new AMap.Pixel(10, 20),
			    //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
			    zoomToAccuracy: true,     
			    //  定位按钮的排放位置,  RB表示右下
			    buttonPosition: 'RB'
			  })
			
			  geolocation.getCurrentPosition()
			  AMap.event.addListener(geolocation, 'complete', onComplete)
			  AMap.event.addListener(geolocation, 'error', onError)
			
			  function onComplete (data) {
			    // data是具体的定位信息
			    var city = data.addressComponent
			    $.ajax({
			    	type:"get",
			    	url:"https://restapi.amap.com/v3/weather/weatherInfo?key=bddc5f06fe17194c662dcc88cc0ce06d&city=" + city.adcode,
			    	async:true,
			    	success: function (data) {
			    		if(data.status == 1) {
			    			_this.weather = data.lives[0]
			    			_this.weather.city  =  city.city
			    		}
			    	}
			    });
			  }
			
			  function onError (data) {
			    // 定位出错
			  }
			})
		}
	}
</script>

<style scoped lang="less">
	.title {
		margin-left: 25px;	
	}
	
	.success {
		background: #e1e1e1;
		color: #fff;
		padding: 3px 15px;
		border-radius: 5px;
		float: right;
	}
	
	.createBotany-content {
		
		.createBotany-updata {
			padding: 0 20px 10px;
			background: #fff;
			
			.createBotany-location {
				display: flex;
				padding: 20px 0;
				align-items: center;
				
				img {
					width: 12px;
					height: 15px;
					margin-right: 10px;
				}
				
				div {
					margin-right: 10px;
				}
			}
			
			.createBotany-updata-img {
				flex-wrap: wrap;
				display: flex;
				
				.imgs {
					position: relative;
					
					img:last-child {
						position: absolute;
						top: -8px;
						right: -8px;
						width: 20px;
						height: 20px;
					}
				}
				
				.add-img {
					position: relative;
					
					input {
						width: 100px;
						height: 100px;
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0;
					}
				}
				
				img {
					width: 100px;
					height: 100px;
					margin-right: 10px;
					margin-bottom: 10px;
				}
			}
		}
	}
	
	.createBotany-address {
		padding: 10px;
		background: #fff;
		margin: 5px 0;
		display: flex;
		align-items: center;
		
		img {
			width: 12px;
			height: 16px;
			margin-top: -4px;
			margin-right: 10px;
		}
	}
	
	.createBotany-bottom {
		
		.createBotany-name {
			padding: 10px 10px 0 ;
			background: #fff;
			margin-bottom: 1px;
			
			.createBotany-name-title {
				font-size: 16px;
				margin: 10px 0;
			}
			
			textarea {
				margin: 0;
				font-size: 13px;
				padding: 0;
			}
		}
		
		.createBotany-dynamic {
			padding: 10px 10px 0;
			background: #fff;
			
			.createBotany-dynamic-title {
				font-size: 16px;
				margin: 10px 0;
			}
			
			textarea {
				margin: 0;
				font-size: 13px;
				padding: 0;
			}
		}
	}
</style>


